<template>
  <v-card class="pa-16" style="color: #686868">
    <CardTitle label=" " color="#f1c60a" />
    <v-row>
      <v-col cols="12" md="4">    : {{ itemData.name }} </v-col>
      <v-col cols="12" md="4">  : {{ itemData.fatherName }} </v-col>
      <v-col cols="12" md="4">  : {{ itemData.birthDate }} </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="4">  : {{ itemData.nationalCode }} </v-col>
      <v-col cols="12" md="4">
         : {{ itemData.certificateNumber }}
      </v-col>
      <v-col cols="12" md="4"> : 
        
        <span v-show="itemData.gender == 0"></span>
        <span v-show="itemData.gender == 1"></span> </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="4"> : {{ itemData.country.name }} </v-col>
      <v-col cols="12" md="4"> : {{ itemData.state.name }} </v-col>
      <v-col cols="12" md="4"> : {{ itemData.city.name }} </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="4">  : {{ itemData.mobile }} </v-col>
      <v-col cols="12" md="4">  : {{ itemData.phone }} </v-col>
      <v-col cols="12" md="4">
        :
        <span class="vira-en">{{ itemData.email }}</span>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="8">  : {{ itemData.address }} </v-col>
      <v-col cols="12" md="4">  : {{ itemData.postalCode }} </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="4">
          :
        <span class="vira-en">{{ itemData.username }}</span>
      </v-col>
      <v-col cols="12" md="4">
         : <span class="vira-en">{{ itemData._id }}</span>
      </v-col>
      <v-col cols="12" md="4">
          : {{ itemData.referenceCode }}
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="4">
        :

        <span v-show="itemData.status == -1"> </span>
        <span v-show="itemData.status == 0">  </span>
        <span v-show="itemData.status == 1"></span>
      </v-col>
      <v-col cols="12" md="4">
         :

        <span v-show="itemData.type == 0"></span>
        <span v-show="itemData.type == 1"></span>
      </v-col>
      <v-col cols="12" md="4">
          : {{ itemData.accepters.length }}
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import CardTitle from "@/components/CardTitle";
export default {
  name: "Create",
  components: {
    CardTitle,
  },
  data() {
    return {
      itemData: {},
    };
  },
  methods: {
    getItemData() {
      return new Promise((resolve, reject) => {
        this.$http
          .get(`${this.$privateKey}/agent/${this.$route.params.id}`)
          .then((res) => {
            this.itemData = res.data.ASREVIRA;
            this.itemData.country = this.findCountry(this.itemData.country);
            this.itemData.state = this.findState(this.itemData.state);
            this.itemData.city = this.findCity(this.itemData.city);
            resolve();
          })
          .catch((err) => {
            console.error(err);
            reject(err);
          });
      });
    },

    findCity(id) {
      return this.allCities.find((city) => city.id == id);
    },
    findState(id) {
      return this.allStates.find((state) => state.id == id);
    },
    findCountry(id) {
      return this.allCountry.find((country) => country.id == id);
    },
  },

  async beforeMount() {
    await this.$http.get(process.env.VUE_APP_API_COUNTRY).then((res) => {
      this.allCountry = res.data;
    });
    await this.$http.get(process.env.VUE_APP_API_PROVINCE).then((res) => {
      this.allStates = res.data;
    });
    await this.$http.get(process.env.VUE_APP_API_CITY).then((res) => {
      this.allCities = res.data;
    });
    await this.getItemData();
  },
};
</script>
