<template>
  <v-col :cols="cols" :sm="sm" :md="md" :lg="lg" :xl="xl">
    <v-text-field
      :label="label"
      :rules="validRules"
      :value="value"
      :class="isEng(value) ? `english-font vira-ltr` : ''"
      :append-icon="appendIcon"
      @input="$emit('input', $event)"
      required
    />
  </v-col>
</template>

<script>
export default {
  name: "Texts",
  props: {
    label: { type: String, default: "" },
    appendIcon: { type: String, default: "" },
    value: { type: String, default: "" },
    validations: { type: Array, default: null },
    cols: { type: String, default: "12" },
    sm: { type: String, default: null },
    md: { type: String, default: null },
    lg: { type: String, default: null },
    xl: { type: String, default: null },
  },
  data() {
    return {
      rules: {
        required: (value) => !!value || "   ",
        persianAndEnghlish: (v) => {
          const persianAndEnghlish = /^[A-Za-z0-9-   -]+$/;
          if (!v == "") {
            return (
              persianAndEnghlish.test(v) || "        "
            );
          } else {
            return;
          }
        },
        persian: (v) => {
          const persian = /^[-   -]+$/;
          if (!v == "") {
            return persian.test(v) || "   ";
          } else {
            return;
          }
        },
        persianCharachter: (v) => {
          const persianCharachter = /^[-  ]+$/;
          if (!v == "") {
            return persianCharachter.test(v) || "    ";
          } else {
            return;
          }
        },
        persianNumber: (v) => {
          const persianNumber = /^[-]+$/;
          if (!v == "") {
            return persianNumber.test(v) || "    ";
          } else {
            return ;
          }
        },
        english: (v) => {
          const english = /^[A-Za-z0-9]+$/;
          if (!v == "") {
            return english.test(v) || "   ";
          } else {
            return;
          }
        },
        englishCharachter: (v) => {
          const englishCharachter = /^[A-Za-z]+$/;
          if (!v == "") {
            return englishCharachter.test(v) || "    ";
          } else {
            return;
          }
        },
        englishNumber: (v) => {
          const englishNumber = /^[0-9]+$/;
          if (!v == null) {
            return englishNumber.test(v) || "    ";
          } else {
            return;
          }
        },
      },
      validRules: [],
    };
  },
  methods: {
    isEng(str) {
      const en = /^[a-zA-Z0-9]/gi;
      return en.test(str);
    },
  },
  mounted() {
    this.validations.forEach((item) => {
      this.validRules.push(this.rules[item]);
    });
  },
};
</script>
<style lang="scss">
.english-font {
  input {
    font-family: Arial, sans-serif !important;
  }
}
</style>
